.App {
  text-align: center;
  padding: 2rem;
  min-height: 100vh;
  background: linear-gradient(135deg, #1e3c72 0%, #2a5298 100%);
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
  margin-bottom: 2rem;
  filter: drop-shadow(0 0 0.75rem rgba(97, 218, 251, 0.5));
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: rgba(40, 44, 52, 0.9);
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
  text-decoration: none;
  transition: color 0.3s ease;
}

.App-link:hover {
  color: #00d8ff;
  text-shadow: 0 0 8px rgba(97, 218, 251, 0.5);
}

.result-container {
  background: rgba(255, 255, 255, 0.1);
  padding: 1.5rem;
  border-radius: 0.5rem;
  margin-top: 1rem;
  backdrop-filter: blur(5px);
}

.result-text {
  font-size: 1.2rem;
  margin: 0.5rem 0;
  color: #61dafb;
}

input {
  background: transparent;
  border: 2px solid rgba(97, 218, 251, 0.3);
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  margin: 0.5rem;
  color: white;
  font-size: 1rem;
  transition: all 0.3s ease;
}

input:focus {
  outline: none;
  border-color: #61dafb;
  box-shadow: 0 0 8px rgba(97, 218, 251, 0.5);
}

button {
  background: #61dafb;
  color: #282c34;
  border: none;
  border-radius: 0.5rem;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  margin: 1rem;
}

button:hover {
  background: #00d8ff;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(97, 218, 251, 0.3);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.calculator-container {
  background: rgba(255, 255, 255, 0.1);
  padding: 2rem;
  border-radius: 1rem;
  margin: 2rem auto;
  max-width: 400px;
  backdrop-filter: blur(5px);
  position: relative;
  z-index: 0;
}

input[type="number"] {
  width: 80%;
  background: rgba(255, 255, 255, 0.15);
  border: 2px solid rgba(97, 218, 251, 0.3);
  border-radius: 0.5rem;
  padding: 0.75rem 1rem;
  margin: 0.75rem;
  color: white;
  font-size: 1.1rem;
  transition: all 0.3s ease;
  position: relative;
  z-index: 1;
  -moz-appearance: textfield;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.error-message {
  color: #ff6b6b;
  background: rgba(255, 107, 107, 0.1);
  padding: 0.75rem;
  border-radius: 0.5rem;
  margin: 1rem 0;
  font-size: 0.9rem;
}

.fade-in {
  animation: fadeIn 0.5s ease-in;
}

.highlight {
  color: #61dafb;
  text-shadow: 0 0 8px rgba(97, 218, 251, 0.3);
}

.calculating {
  opacity: 0.7;
  cursor: wait;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.analysis-container {
  background: rgba(255, 255, 255, 0.1);
  padding: 2rem;
  border-radius: 1rem;
  margin: 2rem auto;
  max-width: 800px;
  backdrop-filter: blur(5px);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.text-input {
  width: 90%;
  max-width: 600px;
  background: rgba(255, 255, 255, 0.15);
  border: 2px solid rgba(97, 218, 251, 0.3);
  border-radius: 0.5rem;
  padding: 1rem;
  margin: 1rem 0;
  color: white;
  font-size: 1rem;
  font-family: inherit;
  resize: vertical;
}

.text-input:focus {
  outline: none;
  border-color: #61dafb;
  box-shadow: 0 0 8px rgba(97, 218, 251, 0.5);
}

.analysis-text {
  text-align: left;
  background: rgba(0, 0, 0, 0.2);
  padding: 1rem;
  border-radius: 0.5rem;
  overflow-x: auto;
}

.analysis-text pre {
  margin: 0;
  white-space: pre-wrap;
  color: #61dafb;
}

.analyzing {
  opacity: 0.7;
  cursor: wait;
}

.results-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2rem;
}

.results-container h3 {
  color: #61dafb;
  margin-bottom: 1.5rem;
  font-size: 1.8rem;
  text-shadow: 0 0 10px rgba(97, 218, 251, 0.3);
}

.analysis-section {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 0.5rem;
  padding: 1.5rem;
  margin: 1rem 0;
  width: 90%;
  max-width: 600px;
  transition: all 0.3s ease;
}

.analysis-section:nth-child(1) {
  background: rgba(97, 218, 251, 0.1);
  border-left: 4px solid rgba(97, 218, 251, 0.5);
}

.analysis-section:nth-child(2) {
  background: rgba(149, 97, 251, 0.1);
  border-left: 4px solid rgba(149, 97, 251, 0.5);
}

.analysis-section:nth-child(3) {
  background: rgba(97, 251, 189, 0.1);
  border-left: 4px solid rgba(97, 251, 189, 0.5);
}

.analysis-section:nth-child(4) {
  background: rgba(251, 176, 97, 0.1);
  border-left: 4px solid rgba(251, 176, 97, 0.5);
}

.section-content {
  color: white;
  font-size: 1rem;
}

.section-content ul {
  margin: 0;
  padding-left: 1.5rem;
}

.section-content li {
  margin: 0.5rem 0;
}

.section-content p {
  margin: 0.5rem 0;
}

.input-controls {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.button-group {
  display: flex;
  gap: 1rem;
  justify-content: center;
  flex-wrap: wrap;
}

.upload-btn {
  background: #4CAF50;
  color: white;
  border: none;
  border-radius: 0.5rem;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  margin: 1rem;
}

.upload-btn:hover {
  background: #45a049;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(76, 175, 80, 0.3);
}

input[type="file"]:hover {
  cursor: pointer;
}

.editable-content {
  width: 100%;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  padding: 8px;
  color: white;
  font-size: 1rem;
  resize: vertical;
  min-height: 36px;
  margin: 4px 0;
}

.editable-content:focus {
  outline: none;
  border-color: #61dafb;
  box-shadow: 0 0 0 2px rgba(97, 218, 251, 0.3);
}

.download-btn {
  margin-top: 20px;
  background: #4CAF50;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.download-btn:hover {
  background: #45a049;
  transform: translateY(-2px);
}
